'use strict';

/**
* Open a file.
*
* @module @stdlib/fs-open
*
* @example
* var closeSync = require( '@stdlib/fs-close' ).sync;
* var open = require( '@stdlib/fs-open' );
*
* function onOpen( error, fd ) {
*     if ( error ) {
*         throw error;
*     }
*     closeSync( fd );
* }
* open( __filename, onOpen );
*
* @example
* var closeSync = require( '@stdlib/fs-close' ).sync;
* var openSync = require( '@stdlib/fs-open' ).sync;
*
* var fd = openSync( __filename );
* if ( fd instanceof Error ) {
*     throw fd;
* }
* closeSync( fd );
*/

// MODULES //

var setReadOnly = require( '@stdlib/utils-define-nonenumerable-read-only-property' );
var main = require( './main.js' );
var sync = require( './sync.js' );


// MAIN //

setReadOnly( main, 'sync', sync );


// EXPORTS //

module.exports = main;
